/* CSS Document */
/* CSS Document */
* {
  margin: 0;
  padding: 0;
}
.clear {
  clear: both;
}
.wrapper {
  width: 1000px;
  height: auto;

  margin: auto;
}
.header {
  width: 100%;
  height: 200px;
}
.menu {
  width: 100%;
  height: 40px;
  margin-top: 2px;
  background: #c40c42;
}
.menu ul {
  width: 100%;
  height: 40px;
  list-style: none;
  padding-left: 10px;
}
.menu ul li {
  float: left;
  line-height: 40px;
  text-align: center;
  width: 80px;
  height: 40px;
}
.menu ul li a {
  text-decoration: none;
  color: #fff;
  font-size: 18px;
  display: block;
}
.menu ul li:hover {
  background: #39f;
}

.content {
  width: 100%;
  height: auto;

  margin-top: 2px;
}
.content_left {
  width: 28%;
  height: auto;
  border: 1px solid #d9d9d9;
  float: left;
  margin-top: 2px;
}
.hangbanchay {
  width: 100%;
  height: auto;

  text-align: center;
  list-style: none;
}
.hangbanchay li {
  width: 100%;
  height: auto;
  border-bottom: 1px dashed #30c;
  margin: auto;
}
.hangbanchay li:hover {
  background: #c40c42;
}
.hangbanchay a {
  text-decoration: none;
  display: block;
}
.hangbanchay p {
  padding: 5px;
  color: skyblue;
  font-weight: bold;
}
.tintucsp {
  width: 100%;
  height: auto;
  list-style: none;
}
.tintucsp li {
  width: auto;
  height: auto;
  padding: 10px;
  border-bottom: 1px dashed #ccc;
}
.tintucsp a {
  text-decoration: none;
  color: #000;
  font-size: 18px;
}
.tintucsp li:hover {
  background: #c40c42;
}
.tieude {
  width: auto;
  height: 40px;
  background: #c40c42;
  font-size: 20px;
  text-align: center;
  padding: 5px;
  color: #fff;
  line-height: 40px;
}
.box_list {
  width: 98%;
  height: auto;

  margin: 2px;
}
.list {
  width: 100%;
  height: 100%;
  list-style: none;
}
.list li {
  width: auto;
  height: 20px;
  padding-left: 10px;
  padding-top: 5px;
  background: #f2f2f2;
}
.list li a {
  text-decoration: none;
  color: #000;
  display: block;
}
.list li:hover {
  background: #c40c42;
}
.content_right {
  width: 71%;
  height: auto;
  border: 1px solid #d9d9d9;
  float: right;
  margin-top: 2px;
}
.box_product {
  width: 98%;
  height: auto;

  margin: auto;
}
.product {
  list-style: none;
  width: 98%;
  height: auto;
  margin: auto;
}
.product li {
  width: 217px;
  height: 300px;
  text-align: center;
  padding-top: 5px;
  float: left;
  margin: 5px;
}
.product a {
  text-decoration: none;
}
.product li:hover {
  background: #c40c42;
}
.product p {
  padding-top: 2px;
  color: #000;
  font-size: 18px;
}
.footer {
  width: 100%;
  height: 120px;
  border: 1px solid #000;
  background: #c40c42;
  margin-top: 5px;
}
/* chi tiet san pham*/
.box_chitietsp {
  width: 98%;
  height: 300px;

  margin: 2px;
}
.box_hinhanh {
  width: 35%;
  height: 300px;

  float: left;
}
.hinhanhphongto {
  width: 100%;
  height: 100px;
  list-style: none;
}
.hinhanhphongto li {
  width: 70px;
  height: 70px;
  float: left;
  margin-left: 5px;
  border: 1px solid #ccc;
}

.box_info {
  width: 64%;
  height: 300px;
  border-left: 1px solid #000;
  float: right;
  padding-left: 5px;
}
.box_info p {
  margin: 10px;
}
.box_detail {
  width: 100%;
  height: 400px;
}
.button_dathang {
  width: 200px;
  height: 40px;
  border: 1px solid #000;
  border-radius: 25px;
  margin: 10px;
  margin-top: 40px;
}
.button_dathang p {
  font-size: 20px;
  font-weight: bold;

  text-align: center;
  line-height: 40px;
}
.button_dathang a {
  color: #0c9;
  text-decoration: none;
  display: block;
}
.button_dathang:hover {
  background: #c40c42;
}
.sanphamlienquan {
  width: 100%;
  height: 250px;
}
.sanphamlienquan ul {
  width: auto;
  list-style: none;
  height: auto;
}
.sanphamlienquan ul li {
  width: 215px;
  height: auto;

  text-align: center;
  margin: 8px;
  float: left;
}
.sanphamlienquan a {
  text-decoration: none;
  color: skyblue;
}
.hinhanhsp {
  width: 100%;
  height: auto;
  margin: auto;
  list-style: none;
}
.hinhanhsp img {
  width: 600px;
  margin-top: 10px;
}
.hinhanhsp li {
  text-align: center;
}
/*Gio hang*/
.box_giohang {
  width: 98%;
  height: auto;

  margin: 2px;
}
.button_capnhapgiohang {
  width: 150px;
  height: 40px;
  line-height: 40px;
  border: 1px solid #000;
  border-radius: 25px;
  margin: 5px;
  float: right;
}
.button_capnhapgiohang:hover {
  background: #0c3;
}
.button_capnhapgiohang a {
  display: block;
  color: red;
  font-size: 18px;
  text-decoration: none;
}
.control p {
  font-size: 18px;
  color: skyblue;
  margin: 10px;
  float: left;
  color: blue;
  text-decoration: none;
}
.control a {
  text-decoration: none;
}
.control a:hover {
  text-decoration: underline;
}
/*Dang ky*/
.thongbao {
  width: 100%;
  height: 200px;
  border: 1px solid #d9d9d9;
}
.thongbao p {
  font-size: 22px;
  color: red;
  margin: 2px;
}
.dangky tr td {
  padding: 5px;
}
.dangky input {
  width: 100%;
  height: 20px;
}
.ghichu textarea {
  width: 100%;
  height: 60px;
}
.ghichu p {
  margin: 5px;
  text-decoration: underline;
}
.button_gui {
  width: 100px;
  height: 100px;
  border: 1px solid #000;
  border: 25px;
}
.tabs_panel ul {
  list-style-type: none;
}
.tabs_panel .tabs li {
  list-style-type: none;
  display: inline-block;
  padding: 10px 20px;
  background: #c40c42;
  border-radius: 10px 10px 0 0;
  color: #fff;
  font-weight: 200;
  cursor: pointer;
}
.tabs_panel ul li.active {
  color: #000;
  background: #9f0;
}
.tabs_panel .panel {
  display: none;
  background: #fff;
  color: #000;
  padding: 30px;
}
.tabs_panel .panel.active {
  display: block;
}
.scroll-to-top {
  display: fixed;
}
